<script>
import { Doughnut } from 'vue-chartjs'

export default {
  extends: Doughnut,
  mounted () {
    this.renderChart(
      {
        labels: ['Mac OS', 'Windows', 'Linux'],
        datasets: [
          {
            data: [60, 20, 10, 10],
            backgroundColor: ['#dbc5ec', '#BD9DD5', '#A67DC5', 'transparent'],
            borderColor: 'transparent'
          }
        ]
      },
      {
        responsive: true, maintainAspectRatio: false
      }
    )
  }
}
</script>
