import {
  Object3D,
  MeshPhongMaterial,
  Mesh,
  BoxBufferGeometry,
  DoubleSide
} from 'three'

export default class Ground extends Object3D {
  constructor () {
    super()

    this.init()
  }

  init () {
    const geometry = new BoxBufferGeometry(80, 80, 10)
    const material = new MeshPhongMaterial({
      color: 0x7a3a00,
      specular: 0x656900,
      shininess: 6,
      side: DoubleSide,
      wireframe: false
    })
    material.flatShading = true

    const ground = new Mesh(geometry, material)
    ground.castShadow = true
    ground.receiveShadow = true
    ground.rotation.x = Math.PI / 2
    ground.rotation.y = Math.PI
    ground.position.set(0, -6, 0)
    this.add(ground)
  }
}
