<template>
 <div class="Canvas3D">
  <canvas class="Canvas3D-canvas" ref="canvas" width="" height="">
    fallback
  </canvas>
 </div>
</template>

<script>
import Webgl from '@/components/canvas/webgl/Webgl.js'

export default {
  mounted () {
    this.webgl = new Webgl(this.$refs['canvas'])
    this.resize()
    window.addEventListener('resize', this.resize)
  },
  beforeDestroy () {
    this.webgl.destroy()
    window.removeEventListener('resize', this.resize)
  },
  methods: {
    resize () {
      this.webgl.onResize()
    }
  }
}
</script>

<style>
 .Canvas3D-canvas{
  max-width: 100%;
  height: 70%;
 }
</style>
