import {
  WebGLRenderer,
  Scene,
  PerspectiveCamera,
  DirectionalLight,
  AmbientLight,
  PointLight,
  /* Audio,
  AudioListener,
  AudioLoader,
  AudioAnalyser, */
  FogExp2,
  Geometry,
  Vector3,
  PointsMaterial,
  Points
} from 'three'

import {
  OrbitControls
} from './controls/OrbitControls'

import Tomato from './objects/Tomato.js'
import Ground from './objects/Ground.js'

export default class Webgl {
  constructor ($object) {
    this.render = this.render.bind(this)
    this.onResize = this.onResize.bind(this)

    /* Init renderer and canvas */
    this.container = $object || document.body
    this.renderer = new WebGLRenderer({
      antialias: true,
      alpha: true,
      canvas: $object
    })
    this.renderer.shadowMap.enabled = true
    // this.renderer.shadowMap.enabled = true

    /* Main scene and camera */
    this.scene = new Scene()
    this.camera = new PerspectiveCamera(60,
      window.innerWidth / window.innerHeight,
      1,
      1000)

    // Fog
    this.scene.fog = new FogExp2(0x11111f, 0.002)
    this.renderer.setClearColor(this.scene.fog.color, 0)

    // Camera
    // this.camera.position.set(200, 200, 400)
    this.camera.position.z = 1
    this.camera.rotation.x = 1.16
    this.camera.rotation.y = -0.12
    this.camera.rotation.z = 0.27
    this.scene.add(this.camera)

    // Lights
    // this.light = new DirectionalLight(0xffffff, 0.7)
    this.light = new DirectionalLight(0xffeedd)
    // this.light.position.set(2, 20, 2)
    this.light.position.set(1, 1, 0)
    this.scene.add(this.light)

    this.ambient = new AmbientLight(0x555555)
    this.scene.add(this.ambient)

    this.point = new PointLight(0x062d89, 30, 500, 1.7)
    this.point.position.set(200, 300, 100)
    this.scene.add(this.point)

    // Orbit controls
    this.controls = new OrbitControls(this.camera, this.renderer.domElement)
    this.verticalAngle = this.controls.getPolarAngle()
    this.horizontalAngle = this.controls.getAzimuthalAngle()
    this.controls.minPolarAngle = this.verticalAngle - 0.5
    this.maxPolarAngle = this.verticalAngle + 0.5
    this.minAzimuthAngle = this.horizontalAngle - 0.5
    this.maxAzimuthAngle = this.horizontalAngle + 0.5
    this.controls.maxDistance = 1

    // Audio
    /* this.listener = new AudioListener()
    this.camera.add(this.listener)

    this.sound = new Audio(this.listener)
    this.scene.add(this.sound)

    this.audioLoader = new AudioLoader()
    this.audioLoader.load('@/components/canvas/utils/Around_The_World.mp3', function (buffer) {
      this.sound.setBuffer(buffer)
      this.sound.setLoop(true)
      this.sound.setVolume(0.5)
      this.sound.play()
    })

    this.analyser = new AudioAnalyser(this.sound, 32)
    this.data = this.analyser.getAverageFrequency() */

    // Ground
    this.ground = new Ground()

    // Rain
    this.rainCount = 9500
    this.cloudParticules = []
    this.rainGeo = new Geometry()
    for (let i = 0; i < this.rainCount; i++) {
      this.rainDrop = new Vector3(
        Math.random() * 400 - 200,
        Math.random() * 500 - 250,
        Math.random() * 400 - 200
      )
      this.rainDrop.velocity = {}
      this.rainDrop.velocity = 0
      this.rainGeo.vertices.push(this.rainDrop)
    }

    this.rainMaterial = new PointsMaterial({
      color: 0xab0000,
      size: 0.5,
      transparent: true
    })

    this.rain = new Points(this.rainGeo, this.rainMaterial)
    this.scene.add(this.rain)

    this.initObjects()
    this.onResize()
    this.render()

    window.addEventListener('resize', this.onResize, false)
  }

  initObjects () {
    const tomatos = []

    for (let i = 0; i < 3; i++) {
      for (let j = 0; j < 6; j++) {
        const tomato = new Tomato({})
        tomato.position.set(i * (-10) + 10, 0, j * (-6) + 14)
        tomatos.push(tomato)
        this.scene.add(tomato)
      }
    }
    this.scene.add(this.ground)
    /* const tomato = new Tomato({})
    tomato.position.set(0, 0, 0)
    this.scene.add(tomato, this.ground) */
  }

  destroy () {
    while (this.scene.children.length > 0) {
      const child = this.scene.children[0]
      if (this.scene.destroy) this.scene.destroy()
      this.scene.remove(child)
    }
    this.renderer.renderLists.dispose()
  }

  onResize () {
    this.renderer.setSize(window.innerWidth, window.innerHeight)
    this.camera.aspect = window.innerWidth / window.innerHeight
    this.camera.updateProjectionMatrix()
  }

  render () {
    this.controls.update()
    this.renderer.render(this.scene, this.camera)
    requestAnimationFrame(this.render)

    // Rain animation
    this.rainGeo.vertices.forEach(p => {
      p.velocity -= 0 * Math.random() * 3
      p.y += p.velocity
      if (p.y < -100) {
        p.y = 100
        p.velocity = 0
      }
    })
    this.rainGeo.verticesNeedUpdate = true
    this.rain.rotation.y += 0.002
  }
}
